* {
  user-select: none;
  margin: 0;
  padding: 0;
}

button, a {
  font-family: "Helvetica-Bold";
  margin: 0;
  background-color: transparent;
  color: #ffe;
  vertical-align: top;
  font-size: 1rem;
}

html {
  background-color: #000000;
  width: 100%;
  height: 100%;
  
  & nav {
    position: fixed;
    width: 100%;
    height: 2rem;
    background: linear-gradient(black, 75%, transparent);
    & header {
      top: 0;
      left: 0;
      width: 100%;
      height: 2rem;
      & img {
        height: 2rem;
      }
      & button {
        border: none;
        padding: 1rem 0.125rem 0.25rem 0.125rem;
        line-height: 0.75rem;
        
      }
    }
    & article {
      color: #ffe;
      text-align: center;
      font-family: "helvetica";
      height: calc(80% - 2rem);
      display: none;
      & button, a {
        display: block;
        margin: 0.5rem auto;
        border: 1px solid #ffe;
        width: 16rem;
        line-height: 2rem;
        box-sizing: border-box;
        text-align: center;
        text-decoration: none;
      }
    }
  }

  & svg {
    margin-top: 2rem;
    vertical-align: top;
  }
  
}

.active {transition: all .25s;}
.active:hover {filter: brightness(0.75);}
.active:active {filter: brightness(0.5);}

.filter {
  filter: brightness(0.75);
}

.time {
  font-family: 'DSEG7Classic-BoldItalic';
  font-size: 10px;
}

.text {
  font-family: 'Kikai Chokoku JIS';
  font-size: 6px;
}

.caption {
  font-family: 'Kikai Chokoku JIS';
  font-size: 4px;
}

.ContentLenderG {
}

.Lever {
  & g {
    transition: transform .2s cubic-bezier(.75, .25, .75, .25);
  }
}